<template>
  <div>
    <v-toolbar
      dense
      class="z-i  d-lg-none d-md-none"
      color="#C59C34"
    >
      <v-btn icon>
        <v-icon>mdi-account</v-icon>
      </v-btn>
      <v-spacer />

      <!-- <v-app-bar-nav-icon @click.stop="drawer=!drawer"></v-app-bar-nav-icon> -->
      <v-btn
        icon
        :to="{name: 'sidebar'}"
      >
        <span v-if="cartSize">{{ cartSize }}</span>
        <v-icon>mdi-shopping-outline</v-icon>
      </v-btn>
    </v-toolbar>
    <div
      class="sidebar d-none d-lg-flex d-md-flex"
    >
      <v-navigation-drawer
        v-model="drawer"
        class="overflow"
        permanent
        color="white"
        right
        absolute
      >
        <v-list-item class="mt-10 py-5">
          <v-list-item-title
            class="text-capitalize font-weight-bold "
            align="center"
          >
            your cart
          </v-list-item-title>
        </v-list-item>
        <v-list
          nav
          dense
        >
          <v-list-item-group v-model="group">
            <v-list-item class="mb-6">
              <v-row justify="center">
                <v-btn-toggle
                  class="mb-4"
                  rounded
                >
                  <v-btn
                    small
                    outlined
                    color="#C59C34"
                  >
                    Delivery
                  </v-btn>
                  <v-btn
                    small
                    color="#C59C34"
                    class="white--text"
                  >
                    Pickup
                  </v-btn>
                </v-btn-toggle>
              </v-row>
            </v-list-item>
          </v-list-item-group>
        </v-list>
        <v-divider v-if="cartSize" />
        <div
          v-if="!cartSize"
          class="text-center font-italic font-weight-light"
        >
          your cart is empty!
        </div>
        <div
          v-for="item in cart"
          :key="item.id"
        >
          <v-row>
            <v-col>
              <v-list
                nav
                dense
              >
                <v-list-item-group>
                  <v-list-item>
                    <v-list-item-content>
                      <v-list-item-title v-if="item.title">
                        {{ item.title }}
                        <p class="font-weight-light">
                          {{ item.price }}
                        </p>
                      </v-list-item-title>
                      <v-list-item-subtitle class="mt-5">
                        <a href="">opend details</a>
                      </v-list-item-subtitle>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-col>
            <v-col>
              <v-btn
                icon
                block
                @click=" deleteFromCart (id)"
              >
                <v-icon>mdi-close</v-icon>
              </v-btn>
              <p class="text-color text-center ">
                {{ item.quantity* item.price }}EUR
              </p>
              <v-btn-toggle>
                <v-btn
                  icon
                  small
                  :disabled="item.quantity === item.stock"
                  @click=" addToCart (item.id)"
                >
                  <v-icon>mdi-plus</v-icon>
                </v-btn>
                <v-btn
                  icon
                  small
                >
                  {{ item.quantity }}
                </v-btn>
                <v-btn
                  :disabled="item.quantity === 1"
                  icon
                  small
                  @click=" removeFromCart (item.id)"
                >
                  <v-icon>mdi-minus</v-icon>
                </v-btn>
              </v-btn-toggle>
            </v-col>
          </v-row>
        </div>
        <!-- <v-divider /> -->
        <v-divider v-if="cartSize" />
        <div v-if="cartSize">
          <v-row>
            <v-col>
              <v-list
                nav
                dense
              >
                <v-list-item-group>
                  <v-list-item>
                    <v-list-item-content>
                      <v-list-item-title class="font-weight-bold">
                        Subtotal
                      </v-list-item-title>
                      <v-list-item-title class="mt-5 font-weight-bold">
                        Total <span class="font-weight-light">(inc.vat)</span>
                      </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list-item-group>
              </v-list>
            </v-col>
            <v-col>
              <p class="text-center mt-2 font-weight-bold">
                <span>0 EUR</span>
              </p>
              <p class="text-center font-weight-bold">
                <span>{{ cartTotalAmount }} EUR</span>
              </p>
            </v-col>
          </v-row>
          <div class="pa-2 text-captalize">
            <v-btn
              block
              color="#08062B"
              class="white--text"
            >
              check out
            </v-btn>
          </div>
        </div>
        <!-- <template v-slot:append /> -->
        <!-- </v-row> -->
      </v-navigation-drawer>
    </div>
  </div>
</template>
<style scoped>
.overflow{
  /* overflow-y: scroll ; */
}
.z-i{
    z-index: 400;
}
.text-color{
    color: #C59C34;
}
span{
    font-size: 14px;
}
</style>
<script>
import { mapState, mapGetters } from 'vuex'
export default {
  data () {
    return {
      drawer: true,
      group: true,
    }
  },
  computed: {
    ...mapState([
      'cart',
    ]),
    ...mapGetters([
      'cartSize',
      'cartTotalAmount',
    ]),
  },
  methods: {
    addToCart (id) {
      this.$store.dispatch('addToCart', id)
      // this.$cookies.get('cart', JSON.parse(this.cart))
    },
    removeFromCart (id) {
      this.$store.dispatch('removeFromCart', id)
    },
    deleteFromCart (id) {
      this.$store.dispatch('deleteFromCart', id)
    },
  },
}
</script>
